.Header_cart_button_bump__20pHa {
	-webkit-animation: Header_cart_button_bump__20pHa 300ms ease-out;
	        animation: Header_cart_button_bump__20pHa 300ms ease-out;
}

@-webkit-keyframes Header_cart_button_bump__20pHa {
	0% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	10% {
		-webkit-transform: scale(0.9);
		        transform: scale(0.9);
	}
	30% {
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
	}
	50% {
		-webkit-transform: scale(1.15);
		        transform: scale(1.15);
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

@keyframes Header_cart_button_bump__20pHa {
	0% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
	10% {
		-webkit-transform: scale(0.9);
		        transform: scale(0.9);
	}
	30% {
		-webkit-transform: scale(1.1);
		        transform: scale(1.1);
	}
	50% {
		-webkit-transform: scale(1.15);
		        transform: scale(1.15);
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

